@font-face {
    font-family: 'austein';
    src: url('./assets/fonts/Austein/Austein.woff2') format('woff2');
    font-weight: normal;
    font-style: italic;

}
*   {
    padding: 0;
    margin: 0;
    list-style: none;
    background-color: darkslateblue;
    font-family: 'Poppins', sans-serif;
}
/********************* Header *********************/
.header-container  {
    display: flex;
    justify-content: space-between;
    background-color: whitesmoke;
    height: 4rem;
}
.header-item   {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20%;
    margin-right: 20%;
    width: 20rem;
}
.header-item-1 {
    justify-content: flex-start;
    min-width: 20vh;
    background-color: whitesmoke;
    font-family: 'Austein', sans-serif;
    font-style: italic;
    font-size: 2rem;
    color: darkslateblue;

}
.header-item-2 {
    justify-content: flex-end;
    background-color: whitesmoke;
}
.li-navbar {
    margin: 10px;
    background-color: whitesmoke;
}
a   {
    color: black;
    text-decoration: none black;
}
/********************* Body *********************/
/********************* Footer *********************/
.footer-container   {
    display: flex;
    flex-direction: row;
    margin-top: 1.5rem;
    margin-left: 20%;
    margin-right: 20%;
    justify-content: space-between;
    height: 10rem;
}
.footer-title   {
    color: dodgerblue;
}
.footer-item    {
    color: whitesmoke;
}
.fa-brands  {
    color: dodgerblue;
    width: 3rem;
}
